#home *{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}
#home body {
    background-color: #00cc66;
}
#home .container{
    background-color: #333333;
    width: 90%;
    height: 82vh;
    margin: auto;
    margin-top: 35px;
    border-radius: 50px;
    padding: 0 8%;
    display: flex;
    justify-content: center;
    align-items: center;
}
#home h1 {
    color: white;
    font-size: 40px;
    padding-bottom: 15px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    animation: h1 4s ;
}
@keyframes h1 {
    0% {
        color: #333333;
    }
}
#home h2 {
    color: #00cc66;
    font-size: 30px;
    padding-bottom: 15px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    animation: h2 4s steps(17);
}
#home p {
    color: white;
    font-size: 21px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    padding-bottom: 15px;
}
#home i {
    color: white;
    font-size: 25px;
    padding-top: 40px;
}
#home .in{
    padding-left: 30px;
}
#home img {
    width: 100%;
    margin-left: 50px;
    animation: img 4s;
}
@keyframes img {
    0%{
        color: #333333;
        margin-left: 80px;
    }
    30%{
        margin-left: 50px;
    }
}