#page * {
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}

#page body {
    background-color: #00cc66;
}

#page .card {
    background-color: #333333;
    margin: 30px;
    width: 300px;
    height: 400px;
    text-align: center;
    display: inline-block;
    padding: 10px;
    border-radius: 30px;
}

#page img {
    width: 250px;
}

#page p {
    color: #00cc66;
    margin: 15px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

#page button {
    width: 80px;
    height: 23px;
    border-radius: 30px;
    border: none;
    outline: none;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

#page button:active {
    transform: scale(1.1);
    background-color: #00cc66;
    color: white;
}

#page table,td,th {
    width: 250px;
    background-color: #333333;
    color: white;
}

.employeeDetails table {
    display: inline-block;
}
.employeeDetails {
    text-align: center;
    padding-top: 50px ;
}
