#e-card * {
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}

#e-card body {
    background-color: #00cc66;
}

.e-card {
    display: inline-block;
    background-color: #333333;
    margin: 40px;
    width: 250px;
    /* height: 400px; */
    text-align: center;
    padding: 10px;
    border-radius: 30px;
}

.e-card img {
    width: 250px;
}

.e-card p {
    color: #00cc66;
    margin: 15px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    
}

.e-card button {
    width: 80px;
    height: 23px;
    border-radius: 30px;
    border: none;
    outline: none;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    cursor: pointer;
}

.e-card button:active {
    transform: scale(1.1);
    background-color: #00cc66;
    color: white;
}